___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
    background-color: #0F172A;
    padding: 1rem 0.5rem;
    font-size: 1.6rem;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.logo {
    width: 50px;
}

.sidebar-header {
    margin-bottom: 1.3rem;
    padding-left: 0.5rem;
}

.sidebar-sections {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 1.3rem;
    max-height: 100%;
    overflow-y: hidden;
}

.create {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex: 1;
    overflow-y: auto;
    max-height: 100%;
}

.create .sidebar-line:hover {
    cursor: default;
}

.sidebar-line, 
.file-line {
    display: flex;
    justify-content: space-between;
    padding: 0.4rem;
    margin-bottom: 0.1rem;
}

li.file-line{
    list-style: none;
    color: rgb(136, 136, 136);
    font-size: 0.8em;
    padding-left: 1.5rem;
}

.sidebar-line-right {
    padding: 0 0.5rem; 
    display: none;
}

.file-line .sidebar-line-right {
    display: flex;
    gap: 0.5rem;
    display: none;
}

.sidebar-line-right-icon {
    width: 20px;
    border-radius: 5px;
}

.sidebar-add-icon {
    cursor: pointer;
    width: 23px;
    border-radius: 5px;
}

.sidebar-add-icon:hover,
.sidebar-line-right-icon:hover {
    background-color: rgba(255, 255, 255, 0.15);
}

.sidebar-line-left {
    display: flex;
    gap: 0.5rem;
    min-width: 0;
}

.sidebar-line-left p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

li.file-line .sidebar-line-left {
    gap: 0.3rem
}


.sidebar-line:hover,
.file-line:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 5px;
}

.sidebar-line:hover .sidebar-line-right,
.file-line:hover .sidebar-line-right {
    display: flex;
}

.sidebar-icon {
    width: 30px;
}

.task-icon {
    width: 25px;
}

.file-icon {
    width: 24px;
}`, "",{"version":3,"sources":["webpack://./src/css/sidebar.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,oBAAoB;IACpB,iBAAiB;IACjB,aAAa;IACb,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,qBAAqB;IACrB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,OAAO;IACP,WAAW;IACX,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,OAAO;IACP,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,eAAe;AACnB;;AAEA;;IAEI,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;IAChB,yBAAyB;IACzB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,iBAAiB;IACjB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,kBAAkB;AACtB;;AAEA;;IAEI,2CAA2C;AAC/C;;AAEA;IACI,aAAa;IACb,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI;AACJ;;;AAGA;;IAEI,eAAe;IACf,2CAA2C;IAC3C,kBAAkB;AACtB;;AAEA;;IAEI,aAAa;AACjB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf","sourcesContent":[".sidebar {\n    background-color: #0F172A;\n    padding: 1rem 0.5rem;\n    font-size: 1.6rem;\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n}\n\n.logo {\n    width: 50px;\n}\n\n.sidebar-header {\n    margin-bottom: 1.3rem;\n    padding-left: 0.5rem;\n}\n\n.sidebar-sections {\n    display: flex;\n    flex-direction: column;\n    flex: 1;\n    gap: 1.3rem;\n    max-height: 100%;\n    overflow-y: hidden;\n}\n\n.create {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    flex: 1;\n    overflow-y: auto;\n    max-height: 100%;\n}\n\n.create .sidebar-line:hover {\n    cursor: default;\n}\n\n.sidebar-line, \n.file-line {\n    display: flex;\n    justify-content: space-between;\n    padding: 0.4rem;\n    margin-bottom: 0.1rem;\n}\n\nli.file-line{\n    list-style: none;\n    color: rgb(136, 136, 136);\n    font-size: 0.8em;\n    padding-left: 1.5rem;\n}\n\n.sidebar-line-right {\n    padding: 0 0.5rem; \n    display: none;\n}\n\n.file-line .sidebar-line-right {\n    display: f