dialog {
  width: 400px;
  position: fixed;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem;
  border-radius: 10px;
  color: #0F172A;
}

dialog::backdrop {
  background-color: rgba(2, 5, 11, 0.4);
}
dialog p {
    font-size: 1.5rem;
}

.add-project-form input,
.rename-form input,
.add-note-form input,
.rename-note-form input {
    margin: 1rem 0;
    font-size: 1.3rem;
}

input,
textarea,
select {
  width: 100%;
  font-size: 1rem;
  border: 1.5px solid #dcdee1;
  border-radius: 5px;
  padding: 0.4rem;
  color: #5e5e5e;
  font-family: 'Lato';
}

input:focus {
  border-radius: 4px;
  outline: none; 
  border-color: #285bad;
}

.dialog-btns {
    display: flex;
    gap: 1rem
}

.dialog-btns button {
    font-size: 1.3rem;
    background: #405983;
    padding: 0.25rem 0.5rem;
    color: white;
    border: none;
    border-radius: 5px;
    flex: 1;
}

.dialog-btns button:hover {
    cursor: pointer;
}

.dialog-btns button:active {
  background: #384f74; 
}

.project-error,
.note-error {
    font-size: 1rem;
    color: red;
    margin-bottom: 1rem;
}

.hidden {
    display: none;
}

/* TASK DIALOG */

dialog#task-dialog {
  top: 50%;
  left: 50%;
}
.add-task-container {
    display: flex;
    justify-content: center;
}

.add-task-btn {
    font-size: 1.3rem;
    background-color: #38BDF8;
    color: #0F172A;
    transition: all 0.2s ease;
    font-weight: 600;
    padding: 0.4rem 0.8rem;
    border: none;
    cursor: pointer;
    border-radius: 3px;
}

.add-task-btn:active {
  transform: translateY(3px);
}

.task-form-div {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.form-row {
    display: flex;
    flex-direction: column;
}

.form-row label {
    font-size: 1.2rem;
    margin-bottom: 0.2rem;
}

.form-row textarea {
    resize: none;
    width: 100% !important;
}


input[type="date"],
.task-form-div select {
    width: fit-content;
}

#task-dialog .dialog-btns {
    margin-top: 1rem;
}
